<template>
  <div class="container-fluid">
<!--       <div class="toast-body">
        处理成功!
      </div> -->
    <div :class="alert_class"
    v-if=response.config
    >
        {{ response.data.code > 0 ? '处理成功' : '处理失败' }}
        <br>
        <small v-if="response.data.code==-1"> 其它提示信息: {{ response.data.data }} </small>
    </div>

    <form class="mt-3">
      <div class="mb-3 input-group">
      <select class="form-select"
      v-model=event.level
      >
          <option 
          v-for="(v,key,i) in Event_level"
          :key=i
          :value=i
          :selected="Number(i)==Number(event.level)"
          >
            {{ v }}
 <!--            {{ key }}
            {{ i }} -->
          </option>
        </select>
        <span class="input-group-text">事件级别</span>
      </div>
      <div class="mb-3 input-group">
        <!-- <label class="form-label">事件单号</label> -->
        <input class="form-control" v-model=event.number id="">
        <span class="input-group-text">事件单号</span>
      </div>
      <div class="mb-3 input-group">
        <!-- <label class="form-label">处理人</label> <small class="form-text">自动生成 不可更改.</small> -->
        <!-- <span class="input-group-text">张三</span> -->
        <input class="form-control" type="text" v-model=event.dealer disabled="disabled">
        <span class="input-group-text">处理人</span>
      </div>
      <div class="mb-3 input-group">
        <!-- <label class="form-label">处理建议</label> -->
        <textarea class="form-control" v-model=event.remark></textarea>
        <span class="input-group-text">处理建议</span>
      </div>
      <!-- <button type="submit" class="btn btn-primary">提交处理</button> -->
      <a class="btn btn-success" @click="event_patch()">
        <svg t="1604366601322" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="2654" width="20" height="20"><path d="M439.488 960l124.416-169.984-124.416-35.84L439.488 960 439.488 960 439.488 960M0 559.936l353.472 107.072 435.328-369.6-337.408 398.144 377.92 116.736L1024 64.064 0 559.936 0 559.936 0 559.936M0 559.936" p-id="2655" fill="#ffffff"></path></svg>
        提交处理
      </a>
    </form>


  </div>
</template>
<script>
// @ is an alias to /src
// import HelloWorld from "@/components/HelloWorld.vue";

export default {
  name: "event_detail",
  components: {
    // HelloWorld,
  },
  data() {
    return {
        id: -1,
        event: {
            id: null,
            level: null,
            number: null,
            dealer: null,
            remark: null,
        },
        Event_level: {
            0: "A", 1: "B", 2: "C", 3: "D"
        },
        response: {
            data: {
                code: 0,

            }
        },
    }
  },

  computed: {
    alert_class() {
        return Number(this.response.data.code) > 0 ? 'alert alert-success' : 'alert alert-danger'
    }
  },

  beforeMount() {
  // beforeCreated() {
    console.log("List this.$route.params", this.$route.params);
    this.id = this.$route.params.id ? this.$route.params.id : -2;
    console.log("event_detail this.id", this.id);
    this.$axios.get('/api/v1/emergency/event/' + this.id)
    .then(response=>{
        // console.log("event_detail response", response);
        // console.log("event_detail this.event", this.event);
        this.event = response.data.data.items[0];
        console.log("event_detail this.event", this.event);
    });
  },

  methods: {
    // select_level(){
    //     console.log("event_detail this.event.level", this.event.level);
    // },
    event_patch(){
        console.log("event_detail this.event", this.event);
        this.$axios.patch('/api/v1/emergency/event/' + this.id, JSON.stringify(this.event))
        .then(response=>{
            console.log("event_detail response", response);
            // console.log("event_detail this.event", this.event);
            // this.event = response.data.data.items[0];
            // console.log("event_detail this.event", this.event);
            this.response = response;
        });
    }
  }
};
</script>